import React, { useState } from 'react'
import GeneralInformation from './GeneralInformation'
import Skills from './Skills'
import Education from './Education'
import WorkExperience from './WorkExperience'
import Details from './Details'
import "../App.css"

function App() {
    const [loading, setLoading] = useState(false)
    const [skills, setSkills] = useState([])
    const [inputValues, setInputValues] = useState(

        {
            name: "",
            email: "",
            phoneNumber: "",

            profileBio: "",

            workName: "",
            workLocation: "",
            workDescription: "",

            schoolName: "",
            schoolLocation: "",
            titleOfStudy: "",
            schoolDescription: "",

            github: "",
            website: "",
        }
    )



    function handleClick(e) {
        e.preventDefault()
        console.log(inputValues)
        setLoading(true)
        console.log(skills)
    }


    function handleFormChange(e) {
        const value = e.target.value
        setInputValues({ ...inputValues, [e.target.name]: value })
    }

    function handleCheckbox(e) {
        const isChecked = e.target.checked
        if (isChecked) {
            setSkills([...skills, e.target.value])
        } else {
            const index = [...skills].indexOf(e.target.value);
            [...skills].splice(index, 1)
            setSkills([...skills])
        }
    }

    if (loading) {
        return (
            <div className="container">
                <Details inputValues={inputValues} />
                <Skills skills={skills} />
                <GeneralInformation inputValues={inputValues} />
                <WorkExperience inputValues={inputValues} />
                <Education inputValues={inputValues} />
            </div>)
    }

    return (
        <div className="form-div">
            <form>

                <h2>Profile</h2>
                <input className="input" type="text" onChange={handleFormChange} required placeholder="Name" name="name" />
                <input className="input" type="text" onChange={handleFormChange} required placeholder="Email" name="email" />
                <input className="input" type="text" onChange={handleFormChange} placeholder="Phone Number" name="phoneNumber" />
                <textarea onChange={handleFormChange} required placeholder="Profile Bio" name="profileBio" />

                <h2>Desired Role</h2>
                <input className="input" type="text" onChange={handleFormChange} placeholder="Job Title" name="jobTitle" />

                <h2>Skills</h2>
                <div className="skills-form">
                    <input onChange={handleCheckbox} type="checkbox" name="html" value="HTML" />HTML
                    <input type="checkbox" onChange={handleCheckbox} name="css" value="CSS" />CSS
                    <input type="checkbox" onChange={handleCheckbox} name="javascript" value="JavaScript" />JavaScript
                    <input type="checkbox" onChange={handleCheckbox} name="express" value="Express" />Express
                    <input type="checkbox" onChange={handleCheckbox} name="node" value="Node" />Node
                    <input type="checkbox" onChange={handleCheckbox} name="mongo" value="Mongo" />Mongo DB
                    <input type="checkbox" onChange={handleCheckbox} name="react" value="React" />React
                    <br />
                    <input type="checkbox" onChange={handleCheckbox} name="angular" value="Angular" />Angular
                    <input type="checkbox" onChange={handleCheckbox} name="vue" value="Vue" />Vue
                    <input type="checkbox" onChange={handleCheckbox} name="bootstrap" value="Bootstrap" />Bootstrap
                    <input type="checkbox" onChange={handleCheckbox} name="php" value="PHP" />PHP
                    <input type="checkbox" onChange={handleCheckbox} name="typescript" value="TypeScript" />TypeScript
                    <input type="checkbox" onChange={handleCheckbox} name="ruby" value="Ruby" />Ruby on Rails

                </div>

                <h2>Work Experience</h2>
                <input className="input" type="text" onChange={handleFormChange} placeholder="Work Name" name="workName" />
                <input className="input" type="text" onChange={handleFormChange} placeholder="Work Location" name="workLocation" />
                <textarea onChange={handleFormChange} placeholder="Work Description" name="workDescription" />
                <h2>Education</h2>
                <input className="input" type="text" onChange={handleFormChange} placeholder="School Name" name="schoolName" />
                <input className="input" type="text" onChange={handleFormChange} placeholder="School Location" name="schoolLocation" />
                <input className="input" type="text" onChange={handleFormChange} placeholder="Title Of Study" name="titleOfStudy" />
                <textarea onChange={handleFormChange} placeholder="What did you learn" name="schoolDescription" />
                <h2>Links</h2>
                <input className="input" type="text" onChange={handleFormChange} placeholder="Github ex: https://github.com/" name="github" />
                <input className="input" type="text" onChange={handleFormChange} placeholder="Website ex: https://website.com" name="website" />
                <button className="submit" onClick={handleClick}>Submit</button>
            </form>
        </div >
    )
}

export default App