import React from 'react'
import pfp from "../img/pfp.png"


export default function Details({ inputValues }) {
    const gitHubLink = inputValues.github
    const websiteLink = inputValues.website
    return (
        <div className="details-div left-col">
            <h2>{inputValues.name}</h2>
            <img className="profile-image" src={pfp} alt="" />
            <h3>Web Developer</h3>
            <span>{inputValues.email}</span>
            <br />
            <span>{inputValues.phoneNumber}</span>
            <br />
            <a href={gitHubLink.toString()} target="_blank">Github</a>
            <br />
            <a href={websiteLink.toString()} target="_blank">Website</a>
        </div>
    )
}
